import React from 'react';
import { BrowserRouter, Link, Route, Switch, Redirect } from 'react-router-dom';
import { parse } from 'url';
import { homepage } from '../package.json';
import routers from 'src/routers';
import Loadable from 'react-loadable';

const loadingComponent = () => (
  <div></div>
);

const basename = parse(homepage).pathname;

const PageAbout = Loadable({
  loader: () => import('./pages/about/index.jsx'),
  loading: loadingComponent
});

const App = () => (
  <BrowserRouter basename={basename}>
    <section className="app">
      <header className="app-header">app-header</header>
      <Switch>
        <Route path="/" exact component={routers.PageHome} />
        <Route path="/about/" component={loadingComponent} />
        <Route path="/news/" component={routers.PageNews} />
        <Route path="/products/" component={routers.PageProducts} />
        <Redirect to='/'/>
      </Switch>
      <footer className="app-footer">app-footer</footer>
    </section>
  </BrowserRouter>
);

export default App;



